@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden !important;
}

.navbarMine {
  position: fixed;
  z-index: 1;
  min-height: 10vh;
  width: 100vw;
  background-color: #f8f9fa;
}

.navUl {
  text-align: right;
  z-index: 1;
  margin-right: 20px;
}

.navUl li {
  position: relative;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  color: black;
  list-style: none;
  display: inline;
  padding: 5px 30px;
  line-height: 9vh;
  font-size: 20px;
  transition: 0.2s;
  overflow: hidden;
  opacity: 1;
}

.navUl li:hover {
  color: #107b7b;
  cursor: pointer;
}

.navUl li span {
  margin-top: 2px;
  border-bottom: 2px solid black;
  width: 70%;
  text-align: center;
  position: absolute;
  left: 12%;
  /* transform: translateX(30%); */
  bottom: -40%;
  background-color: black;
  transition: 0.3s cubic-bezier(0.99, 0, 0.98, 0.82);
  opacity: 0;
}

.navUl li:hover span,
.navUl li.active span,
ul .active span {
  bottom: 0;
  left: 12%;
  opacity: 1;
  text-align: center;
}

.menuOrClose {
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform: translate(25%, 50%);
          transform: translate(25%, 50%);
  width: 25px;
  height: 25px;
  transition: 0.4s;
  margin: 10px;
  cursor: pointer;
  display: none;
}

@media screen and (max-width: 850px) {
  .menuOrClose {
    display: block;
  }
  .navUl li {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .navUl li span {
    display: none;
  }
  .navUl {
    display: none;
  }
  .navUl.active {
    display: block;
  }
}

.homeWrapper {
  min-height: 100vh;
  min-width: 100vw;
