.technologies,
.projectsContainer {
  position: relative;
  height: 90vh;
  min-width: 100vw;
  background-image: linear-gradient(#ffffff, #d7e1ec);
  border-top: 1px dotted black;
  font-family: "Roboto Condensed", sans-serif;
}

.technologyHeader,
.projectsHeader {
  position: absolute;
  left: 43%;
  top: 5%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 40px;
}

.technologyHeader .glint,
.projectsHeader .glint,
.contactHeader .glint {
  padding: 0 10px;
}
/* creating grid */
.technologyContainer ul {
  padding-bottom: 20px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  width: 100vw;
  height: calc(90vh - 20%);
  position: absolute;
  top: 20%;
  left: 0;
  /* background-color: black; */
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(9, 1fr);
  grid-template-areas:
    "html html html css css css js js js"
    "html html html css css css js js js"
    "html html html css css css js js js"
    "react react react git git git bootstrap bootstrap bootstrap"
    "react react react git git git bootstrap bootstrap bootstrap"
    "react react react git git git bootstrap bootstrap bootstrap"
    "sass sass sass npm npm npm rwd rwd rwd"
    "sass sass sass npm npm npm rwd rwd rwd"
    "sass sass sass npm npm npm rwd rwd rwd";
}
.html5 {
  grid-area: html;
}
.css3 {
  grid-area: css;
}
.js {
  grid-area: js;
}
.react {
  grid-area: react;
}
.git {
  grid-area: git;
}
.bootstrap {
  grid-area: bootstrap;
}
.sass {
  grid-area: sass;
}
.npm {
  grid-area: npm;
}
.rwd {
  grid-area: rwd;
}

.technologyContainer ul li {
  list-style: none;
  position: relative;
  transition: 0.4s;
}

.technologyContainer ul li:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.technologyContainer ul li p {
  position: absolute;
  bottom: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.glint {
  opacity: 0;
  /* transition: 0.8s; */
}

.glint.active {
  opacity: 1;
}

.projectsContainer {
  background-image: linear-gradient(#d7e1ec 95%, #ffffff);
}

.projectsMain {
  position: absolute;
  top: 25vh;
  left: 3%;
  /* background-color: black; */
}
.projectsMain .carousel {
  float: left;
}

.projectsMain .carousel img {
  width: 60%;
  height: 60%;
  margin-left: 5%;
}

.projectsMain .carousel h1 {
  position: absolute;
  top: 0;
  right: 15vw;
}

.projectsMain .carousel .code,
.projectsMain .carousel .demo {
  top: 25%;
  right: 22vw;
  position: absolute;
  background-color: #f75719;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 20px;
}
.projectsMain .carousel .demo {
  right: 15vw;
}
.projectsMain .carousel .code a,
.projectsMain .carousel .demo a {
  text-decoration: none;
  color: black;
}

.projectsMain .left,
.projectsMain .right {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 50px;
  height: 50px;
  overflow: hidden;
  cursor: pointer;
}

.projectsMain .left {
  left: 0;
}
.projectsMain .right {
  right: 5%;
}

:root {
  --blade-hue: 290;
  --blade-lightness: 50%;
}

.contactContainer {
  overflow-x: visible !important;
  position: relative;
  min-height: 90vh;
  min-width: 100vw;
  /* background-color: black; */
  background-image: linear-gradient(#fdfeff 10%, #1967bb);
  font-family: "Roboto Condensed", sans-serif;
}

.contactHeader {
  position: absolute;
  top: 5vh;
  left: 43%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 40px;
}
.arrowToLightsaber {
  position: absolute;
  bottom: 10vh;
  left: 5vw;
}
.lightsaber {
  overflow-x: visible !important;
  position: relative;
  height: calc(350px + 7rem);
  width: 2rem;
  top: 20vh;
  left: 20vw;
  /* margin-top: 50vh; */
  grid-column: 1/-1;
}

.hilt {
  overflow-x: visible !important;
  position: absolute;
  bottom: 0;
  left: 0;
  cursor: pointer;
  display: block;
  width: 0.75rem;
  height: 7rem;
  background-color: red;
  border-radius: 0 0 4px 4px;
  background-image: linear-gradient(
    silver 0 10px,
    hsl(39 75% 50%) 0 14px,
    silver 0 15px,
    black 0 20px,
    silver 0 22px,
    black 0 25px,
    silver 0 27px,
    black 0 30px,
    silver 0 32px,
    black 0 35px,
    silver 0 37px,
    black 0 40px,
    silver 0 42px,
    black 0 45px,
    silver 0 47px,
    black 0 50px,
    silver 0 60px,
    hsl(39 75% 50%) 60px 80px,
    silver 0 100%
  );
}

#blade {
  overflow-x: visible !important;
  position: absolute;
  background: white;
  height: 350px;
  width: 0.75rem;
  bottom: 7rem;
  border-radius: 100vw 100vw 0 0;
  box-shadow: inset 0 0 4px
      hsl(290 100% 50% / 1),
    0 0 1em hsl(290 100% 50% / 0.8),
    0 0 1.5em hsl(290 100% 50% / 0.7),
    0 0 2em hsl(290 100% 50% / 0.5),
    0 0 3em hsl(290 100% 50% / 0.3),
    0 0 5em hsl(290 100% 50% / 0.2);
  box-shadow: inset 0 0 4px
      hsl(var(--blade-hue) 100% var(--blade-lightness) / 1),
    0 0 1em hsl(var(--blade-hue) 100% var(--blade-lightness) / 0.8),
    0 0 1.5em hsl(var(--blade-hue) 100% var(--blade-lightness) / 0.7),
    0 0 2em hsl(var(--blade-hue) 100% var(--blade-lightness) / 0.5),
    0 0 3em hsl(var(--blade-hue) 100% var(--blade-lightness) / 0.3),
    0 0 5em hsl(var(--blade-hue) 100% var(--blade-lightness) / 0.2);

  transition: -webkit-transform 50ms ease-out;

  transition: transform 50ms ease-out;

  transition: transform 50ms ease-out, -webkit-transform 50ms ease-out;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
}

#blade::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: hsl(290 100% 50%);
  background: hsl(var(--blade-hue) 100% var(--blade-lightness));
  border-radius: inherit;
  -webkit-filter: blur(0.5em);
          filter: blur(0.5em);
  opacity: 1;
  -webkit-animation: pulse linear 5s infinite;
          animation: pulse linear 5s infinite;
}

#blade::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: whitesmoke;
  border-radius: inherit;
  z-index: 10;
}

@-webkit-keyframes pulse {
  50% {
    opacity: 0.5;
  }
}

@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}

#blade.becomeJedi {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

.contactMe {
  position: absolute;
  left: 35vw;
  top: 30vh;
  transition: 0.9s;
  opacity: 0;
}

.contactMe p {
  padding: 20px 40px;
  font-size: 30px;
}

.contactMe.becomeJedi {
  opacity: 1;
}

.contactMe p span {
  font-size: 20px;
  margin-left: 20px;
}

